table {
  border-collapse: collapse;
  border: 2px solid #1a237e;
  letter-spacing: 1px;
  font-size: 0.8rem;
}
thead {
  text-align: center;
}
p {
  margin: 0, 0, 0, 0 !important;
}

td,
th {
  border: 1px solid #b0bec5;
  padding: 10px 20px;
}

th {
  background-color: #e8eaf6;
  text-align: center;
}

td {
  text-align: center;
}

tr:nth-child(even) td {
  background-color: #f5f5f5;
}

tr:nth-child(odd) td {
  background-color: #fafafa;
}
